			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		props: {
			height: {
				type: Number,
				default: 0
			},
		},
		created() {
			var that = this
			// that.windowHeight = getApp().globalData.windowHeight
			// console.log(that.type)
			// var url = ''
			// if(that.type == 3){
			// 	that.titleStr = '送达时间'
			// 	url = 'Helpbuy.GetTimes'
			// }else if(that.type == 4){
			// 	that.titleStr = '排队时间'
			// 	url = 'Helpqueue.GetTimes'
			// }else if(that.type == 5){
			// 	that.titleStr = '帮办时间'
			// 	url = 'Helpwork.GetTimes'
			// }else{
			// 	that.titleStr = '取件时间'
			// 	url = 'Helpsend.GetTimes'
			// }
			// http.sendRequest(url,{}).then(function(res){
			// 	if(res.code == 0){
			// 		that.timeArray = res.info
			// 		console.log(that.timeArray)
			// 		that.$forceUpdate()
			// 	}
			// })
			this.getSectionHeight()
		},
		methods: {
			delateCartNums() {
				if (this.nums > 0) {
					this.nums--
				}
			},
			addCartNums() {
				this.nums++
			},
			leftBtnClick(index) {
				this.selectIndex = index
				const sid = 'header' + index;
				this.toView = sid
				console.log(sid)

			},
			getSectionHeight(){
				var hh = 0;
				for (let i = 0; i < 10; i++) {
					hh = hh + (50 + 112*5);
					this.heightArray.push(hh)
				}
				console.log(this.heightArray)
			},
			scrollChange(event){
				console.log(event.detail.scrollTop)
				for (let i = 0; i < this.heightArray.length; i++) {
					if(this.heightArray[i] > event.detail.scrollTop){
						this.selectIndex = i
						break
					}
				}
			},
			gotocell(obj){
				uni.navigateTo({
